.selectric-wrapper {
	position: relative;
	cursor: pointer;
}

.selectric-responsive {
	width: 100%;
}

.selectric {
	border-radius: 0rem;
	background: transparent;
	position: relative;
	overflow: hidden;
}

.selectric .label {
	display: block;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	margin: 0 38rem 0 10rem;
	font-size: 16rem;
	font-family: 'Inter';
	line-height: 38rem;
	color: #7F9795;
	height: 38rem;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.selectric .button {
	display: block;
	position: absolute;
	right: 0;
	top: 0;
	width: 38rem;
	height: 38rem;
	line-height: 42rem;
	background-color: transparent;
	color: #7F9795;
	text-align: center;
}

.selectric .button:after {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	width: 0;
	height: 0;
	border: 4rem solid transparent;
	border-top-color: #7F9795;
	border-bottom: none;
}

.selectric-focus .selectric {
	border-color: #8818cd;
}

.selectric-hover .selectric {
	border-color: #8818cd;
}

.selectric-hover .selectric .button {
	color: #7F9795;
}

.selectric-hover .selectric .button:after {
	border-top-color: #7F9795;
}

.selectric-open {
	z-index: 9999;
}

.selectric-open .selectric {
	border-color: #8818cd;
}

.selectric-open .selectric-items {
	display: block;
}

.selectric-disabled {
	filter: alpha(opacity=50);
	opacity: 0.5;
	cursor: default;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.selectric-hide-select {
	position: relative;
	overflow: hidden;
	width: 0;
	height: 0;
}

.selectric-hide-select select {
	position: absolute;
	left: -100%;
}

.selectric-hide-select.selectric-is-native {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 10;
}

.selectric-hide-select.selectric-is-native select {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;
	border: none;
	z-index: 1;
	box-sizing: border-box;
	opacity: 0;
}

.selectric-input {
	position: absolute !important;
	top: 0 !important;
	left: 0 !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	margin: 0 !important;
	padding: 0 !important;
	width: 1rem !important;
	height: 1rem !important;
	outline: none !important;
	border: none !important;
	background: none !important;
}

.selectric-temp-show {
	position: absolute !important;
	visibility: hidden !important;
	display: block !important;
}

/* Items box */
.selectric-items {
	display: none;
	position: absolute;
	top: 100%;
	left: 0;
	background: #8818cd;
	border: 1rem solid #8818cd;
	z-index: -1;
	box-shadow: 0 0 10rem -6rem;
	border-radius: 10rem;
}

.selectric-items .selectric-scroll {
	height: 100%;
	overflow: auto;
}

.selectric-above .selectric-items {
	top: auto;
	bottom: 100%;
}

.selectric-items ul,
.selectric-items li {
	list-style: none;
	padding: 0;
	margin: 0;
	font-size: 16rem;
	line-height: 20rem;
	min-height: 20rem;
}

.selectric-items li {
	display: block;
	padding: 10rem;
	color: #fff;
	cursor: pointer;
	font-family: 'Inter';
}

.selectric-items li.selected {
	background: #8818cd;
	color: #fff;
}

.selectric-items li.highlighted {
	background: #fff;
	color: #8818cd;
}

.selectric-items li:hover {
	background: #fff;
	color: #8818cd;
}

.selectric-items .disabled {
	filter: alpha(opacity=50);
	opacity: 0.5;
	cursor: default !important;
	background: none !important;
	color: #666 !important;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.selectric-items .selectric-group .selectric-group-label {
	font-weight: bold;
	padding-left: 10rem;
	cursor: default;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	background: none;
	color: #8818cd;
}

.selectric-items .selectric-group.disabled li {
	filter: alpha(opacity=100);
	opacity: 1;
}

.selectric-items .selectric-group li {
	padding-left: 25rem;
}

.selectric-tabs__form-select--w100 {
	width: 100%;
}