.powered {
	padding-top: 100rem;

	&__items {
		margin: 55rem auto 0;
		max-width: 1150rem;
		padding: 0 15rem;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 40rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 15rem;
		}
	}

	&__item {
		width: 260rem;
		height: 260rem;
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgba(124, 38, 177, 0.3);
		border: 3px solid rgba(78, 13, 118, 0.6);
		border-radius: 30rem;

		img {
			width: 235rem;
			height: 150rem;
			object-fit: contain;
		}
	}
}