.box {
	padding-top: 65rem;

	@media (max-width: 991px) {
		padding-top: 95rem;
	}

	&__content {
		max-width: 1170rem;
		margin: auto;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			justify-content: center;
		}
	}

	&__top {
		width: 100%;
		max-width: 330rem;
		margin: 0 80rem 0 auto;

		@media (max-width: 991px) {
			margin: 0;
		}
	}

	&__holder {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__eth {
		font-family: 'Inter';
		font-weight: 400;
		font-size: 24rem;
		line-height: 150%;
		color: #D3E2E2;

	}

	&__link {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #8818CD;
		border: 1px solid #8818CD;
		border-radius: 32rem;
		height: 32rem;
		font-family: 'Inter';
		font-weight: 400;
		color: #fff;
		padding: 0 34rem 0 16rem;

		&:hover {
			color: #fff;
			opacity: 0.8;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/link.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 10rem;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
		}
	}

	&__price {
		font-family: "BlasterEternal",
			sans-serif;
		font-weight: 700;
		margin-top: 15rem;
		font-size: 48rem;
		color: #fff;
	}

	&__text {
		margin-top: 20rem;
		font-family: 'Inter';
		font-size: 18rem;
		color: #D2D4D4;

	}

	&__graph {
		position: relative;
		margin-top: 25rem;
		width: 646rem;
		height: 405rem;
		border: 1px solid #8818cd;

		&::before {
			content: '';
			background-image: url('../img/svg/planet.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 80rem;
			right: 150rem;
			width: 100rem;
			height: 100rem;
		}

		@media (max-width: 991px) {
			width: 500rem;
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__bottom {
		margin-top: 20rem;
		display: flex;
		justify-content: space-between;
	}

	&__utc {
		font-family: 'Inter';
		font-size: 14rem;
		line-height: 135%;
		color: rgba(255, 255, 255, 0.4);
	}

	&__dates {
		display: flex;

	}

	&__date {
		font-size: 14rem;
		line-height: 135%;
		color: rgba(255, 255, 255, 0.4);

		&--active {
			color: #fff;
		}
	}

	&__date+&__date {
		margin-left: 20rem;
	}
}