html {
	box-sizing: border-box;
	font-size: 0.052083333333333336vw;
	height: 100%;

	@media (max-width: 991px) {
		font-size: 0.18vw;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	font-size: 16rem;
	min-width: 320rem;
	height: 100%;
	font-family: "BoomBox", sans-serif;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #fff;
	transition: all 0.3s ease;

	&:hover {
		color: #8818CD;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	&-home {
		background-image: url('../img/bg.png');
	}

	&-market {
		background-image: url('../img/bg-market.png');
	}

	@media (max-width: 991px) {
		background-size: cover;
	}
}

.main {
	flex: 1 1 auto;
}

.title {
	font-size: 48rem;
	line-height: 150%;
	letter-spacing: 0.05em;
	background: linear-gradient(101.38deg, #C58FE6 1.2%, #8818CD 136.4%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	text-fill-color: transparent;
	text-align: center;

	@media (max-width: 991px) {
		font-size: 38rem;
	}
}

.circle {
	&-img {
		position: absolute;

		&--1 {
			top: -50rem;
			right: 320rem;
			width: 102rem;
			height: 102rem;
		}

		&--2 {
			top: 290rem;
			right: 320rem;
			width: 102rem;
			height: 102rem;
		}

		&--3 {
			top: 100rem;
			right: 70rem;
			width: 102rem;
			height: 102rem;
		}

		&--4 {
			top: -70rem;
			right: 70rem;
			width: 102rem;
			height: 102rem;
		}

		&--5 {
			top: 350rem;
			left: 62rem;
			width: 45rem;
			height: 45rem;
		}

		&--6 {
			top: -110rem;
			left: 328rem;
			width: 58rem;
			height: 58rem;
		}
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	@media (max-width: 991px) {
		display: none;
	}
}

.alien {
	&-img {
		position: absolute;

		&--roadmap {
			width: 277rem;
			height: 194rem;
			top: -120rem;
			right: 160rem;
		}

		&--box {
			width: 150rem;
			height: 124rem;
			top: 0;
			right: 280rem;
		}
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	@media (max-width: 991px) {
		display: none;
	}
}