	.socials {
		margin-top: 35rem;
		display: flex;
		align-items: center;
		justify-content: center;

		&__link {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			background: #8818CD;
			border-radius: 30rem;
			width: 52rem;
			height: 52rem;
			z-index: 1;

			@media (max-width: 991px) {
				width: 92rem;
				height: 72rem;
			}

			img {
				width: 22rem;
				height: 20rem;

				@media (max-width: 991px) {
					width: 32rem;
					height: 30rem;
				}
			}

			&::before {
				content: '';
				position: absolute;
				top: 5rem;
				left: -10rem;
				width: 100%;
				height: 100%;
				border: 1px solid #8818CD;
				border-radius: 30rem;
				transition: all 0.3s ease;
				z-index: -1;
			}

			&:hover {
				&::before {
					top: 0;
					left: 0;
				}
			}
		}

		&__link+&__link {
			margin-left: 20rem;
		}
	}