.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		max-width: 330rem;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70rem);

		@media (max-width: 991px) {
			max-width: 430rem;
		}

		@media (max-width: 767px) {
			margin: 0 15rem;
		}

		@media (max-width: 575px) {
			border-radius: 15rem;
		}
	}

	&__close {
		position: absolute;
		right: 20rem;
		top: -35rem;
		font-size: 20rem;
		color: #000;
		text-decoration: none;
		z-index: 3;

		img {
			width: 30rem;
			height: 30rem;
		}
	}

	&__wrap {
		background: rgba(44, 11, 64, 0.9);
		border: 1px solid rgba(197, 143, 230, 0.5);
		border-radius: 80rem;
		padding: 15rem 65rem;

		@media (max-width: 991px) {
			padding: 25rem 60rem;
		}
	}

	&__icon {
		margin: auto;
		width: 69rem;
		height: 69rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__subtitle {
		margin-top: 10rem;
		font-weight: 900;
		font-size: 14rem;
		line-height: 150%;
		text-align: center;
		color: #DDEAE9;

		@media (max-width: 991px) {
			margin-top: 15rem;
			font-size: 16rem;
		}
	}

	&__text {
		margin-top: 10rem;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 12rem;
		line-height: 150%;
		text-align: center;
		color: #C58FE6;

		@media (max-width: 991px) {
			font-size: 16rem;
		}

		a {
			color: #DDEAE9;

			&:hover {
				color: #C58FE6;
			}
		}
	}
}