.how {
	padding-top: 100rem;

	&__content {
		max-width: 1200rem;
		margin: 55rem auto 0;
		padding: 0 15rem;
	}

	&__top {
		display: flex;
		align-items: flex-end;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__bottom {
		margin-top: 40rem;
		display: flex;
		justify-content: center;

		@media (max-width: 991px) {
			margin-top: 0;
			flex-direction: column;
			align-items: center;
		}
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		background: rgba(78, 13, 118, 0.6);
		box-shadow: 0px 0px 15px #C58FE6;
		border-radius: 50%;
		text-align: center;

		@media (max-width: 991px) {
			margin-top: 40rem;
			width: 385rem;
			border-radius: 30rem;
			padding: 10rem 20rem;
		}

		&--sm {
			@media (min-width: 992px) {
				width: 280rem;
				height: 280rem;
			}
		}

		&--md {
			@media (min-width: 992px) {
				width: 335rem;
				height: 335rem;
			}
		}
	}

	&__item+&__item {
		@media (min-width: 992px) {
			margin-left: 90rem;
		}
	}

	&__num {
		display: none;

		@media (max-width: 991px) {
			position: absolute;
			top: 50%;
			left: -30rem;
			transform: translateY(-50%);
			width: 60rem;
			height: 60rem;
			background: #390B56;
			border-radius: 10rem;
			display: flex;
			align-items: center;
			justify-content: center;

			span {
				font-size: 24rem;
				background: linear-gradient(134.18deg, #C58FE6 -46.37%, #8818CD 152.37%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				background-clip: text;
				text-fill-color: transparent;
				text-shadow: 0px 0px 3px #A52CEF;
			}
		}
	}

	&__subtitle {
		font-size: 20rem;
		line-height: 150%;
		color: #DDEAE9;
	}

	&__text {
		margin-top: 5rem;
		font-family: 'Inter';
		font-weight: 500;
		font-size: 18rem;
		line-height: 150%;
		color: #C58FE6;
		padding: 0 20rem;

		span {
			color: #DDEAE9;
		}
	}

	&__img {
		margin: 0 30rem;
		width: 570rem;
		height: 475rem;

		@media (max-width: 991px) {
			order: -1;
			margin: 0;
			width: 420rem;
			height: 325rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}