.trade {
	padding-top: 200rem;

	@media (max-width: 991px) {
		padding-top: 100rem;
	}

	&__title {
		max-width: 905rem;
		margin: auto;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 40rem;
		max-width: 1000rem;
		margin: 95rem auto 0;
		padding: 0 15rem;

		@media (max-width: 991px) {
			margin-top: 40rem;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 15rem;
		}
	}

	&__item {
		background: #8818CD;
		border: 3px solid #4E0D76;
		border-radius: 30rem;
		padding: 20rem 20rem 80rem;
		text-align: center;

		@media (max-width: 991px) {
			padding: 20rem 10rem 40rem;
		}
	}

	&__icon {
		margin: auto;
		width: 100rem;
		height: 109rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__subtitle {
		margin-top: 15rem;
		font-size: 20rem;
		line-height: 150%;
		color: #DDEAE9;

		@media (max-width: 991px) {
			font-size: 18rem;
		}
	}

	&__text {
		margin-top: 20rem;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18rem;
		line-height: 150%;
		color: #C58FE6;
	}
}