.about {
	margin-top: 100rem;
	padding-top: 40rem;
	padding: 40rem 15rem 60rem;
	background: rgba(13, 20, 20, 0.41);
	box-shadow: 0px 1px 22px #C58FE6;

	@media (max-width: 991px) {
		box-shadow: none;
		background-color: transparent;
	}

	&__title {
		color: #DDEAE9;
		-webkit-text-fill-color: inherit;
	}

	&__desc {
		max-width: 855rem;
		margin: 30rem auto 0;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18rem;
		line-height: 150%;
		text-align: center;
		color: #C58FE6;

		span {
			color: #DDEAE9;
		}
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 60rem;
		max-width: 1010rem;
		margin: 45rem auto 0;

		@media (max-width: 991px) {
			grid-template-columns: repeat(1, 1fr);
			grid-gap: 20rem;
		}
	}

	&__item {
		padding: 40rem 30rem;
		background: #1D0928;
		border: 1px solid #4E0D76;
		border-radius: 30rem;
		text-align: center;

		@media (max-width: 991px) {
			width: 320rem;
			margin: auto;
		}
	}

	&__box {
		font-size: 18rem;
		line-height: 150%;
		color: #DDEAE9;
	}

	&__text {
		margin-top: 5rem;
		font-family: 'Inter';
		font-weight: 400;
		color: #C58FE6;
	}
}