.hiring {
	padding-top: 100rem;

	&-market {
		@media (max-width: 991px) {
			display: none;
		}

		.hiring__content {
			max-width: 640rem;
			padding: 33rem 24rem;
		}

		.hiring__title {
			font-size: 20rem;
		}

		.hiring__btn {
			padding: 10rem 36rem;
			font-size: 18rem;
		}

		.hiring__logo {
			width: 155rem;
			height: 52rem;
		}
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		background: linear-gradient(92.04deg, #C58FE6 -25.72%, #8818CD 155.81%);
		box-shadow: 0px 4px 22px #C58FE6;
		border-radius: 30rem;
		max-width: 1130rem;
		margin: auto;
		padding: 70rem 40rem;

		@media (max-width: 991px) {
			flex-direction: column;
			margin: 0 15rem;
			padding: 30rem;
		}
	}

	&__logo {
		width: 265rem;
		height: 84rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__title {
		font-size: 40rem;
		line-height: 150%;
		letter-spacing: 0.165em;
		color: #fff;
		text-shadow: 0px 4px 8px #C58FE6;
		border-bottom: 3px solid #fff;

		@media (max-width: 991px) {
			margin: 40rem 0;
			font-size: 30rem;
		}
	}

	&__btn {
		background: #4E0D76;
		box-shadow: 0px 0px 10px #CA98E8;
		border-radius: 30rem;
		font-family: 'Inter';
		font-style: normal;
		font-weight: 400;
		font-size: 24rem;
		line-height: 150%;
		letter-spacing: 0.15em;
		color: #fff;
		padding: 15rem 70rem;

		&:hover {
			color: #fff;
			opacity: 0.7;
		}
	}
}