.inspired {
	padding-top: 100rem;

	&__content {
		display: flex;
		align-items: center;
		max-width: 1125rem;
		margin: 85rem auto 0;
		padding: 0 15rem;

		@media (max-width: 991px) {
			margin-top: 40rem;
			flex-direction: column;
		}
	}

	&__icon {
		width: 410rem;
		height: 205rem;

		@media (max-width: 991px) {
			width: 300rem;
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__img {
		width: 724rem;
		height: 488rem;

		@media (max-width: 991px) {
			width: 500rem;
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}