.tabs {
	position: relative;
	z-index: 1;
	width: 360rem;

	@media (max-width: 991px) {
		width: 100%;
	}

	&__list {
		display: flex;
		border: 2px solid #C58FE6;
		border-radius: 30rem 30rem 0 0;

		@media (max-width: 991px) {
			border-radius: 0;
		}
	}

	&__wrap {
		background: rgba(78, 13, 118, .3);
		border: 2px solid #c58fe6;
		border-radius: 0 0 30rem 30rem;

		@media (max-width: 991px) {
			border-radius: 0;
			border: none;
			background: transparent;
		}
	}

	&__item {
		width: 100%;

		&:first-child {
			.tabs__btn {
				border-radius: 26rem 0 0 0;
				border-right: 1px solid #C58FE6;

				@media (max-width: 991px) {
					border-radius: 0;
				}
			}
		}

		&:last-child {
			.tabs__btn {
				border-left: 1px solid #C58FE6;
				border-radius: 0 26rem 0 0;
				padding: 15rem 31rem;

				@media (max-width: 991px) {
					border-radius: 0;
				}
			}
		}
	}

	&__btn {
		padding: 15rem 37rem;
		border: none;
		cursor: pointer;
		font-size: 20rem;
		line-height: 150%;
		color: #fff;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
		height: 100%;

		@media (max-width: 991px) {
			width: 100%;
			border-radius: 0;
			padding: 35rem;
		}

		@media (max-width: 575px) {
			padding-left: 0;
			padding-right: 0;
		}

		&:hover {
			color: #4f4f4f;
		}

		&--active {
			background: #C58FE6;
			transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
			pointer-events: none;
		}
	}

	&__settings {
		pointer-events: none;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 32rem;
		height: 32rem;

		@media (max-width: 991px) {
			margin: auto;
			width: 45rem;
			height: 45rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__content {
		display: none;
		padding: 40rem 27rem;

		&--active {
			display: block;
		}
	}

	&__form {
		&-title {
			font-family: 'Inter';
			font-weight: 400;
			font-size: 18rem;
			line-height: 150%;
			color: #D3E2E2;
			text-align: center;
		}

		&-group {
			margin-top: 15rem;
			background: rgba(17, 24, 24, 0.5);
			border: 1px solid #C58FE6;
			border-radius: 30rem;
			height: 60rem;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 20rem;
		}

		&-input {
			width: 50rem;
			background: transparent;
			color: #fff;
			outline: none;

			@media (max-width: 991px) {
				width: 53rem;
			}
		}

		&-select {
			background-color: transparent;
			border: none;
			width: 140rem;
			font-family: 'Inter';
			text-align: right;
			color: #7F9795;

			&--w100 {
				width: 100%;
				text-align: left;
			}
		}

		&-caption {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		&-percent {
			background: rgba(17, 24, 24, 0.5);
			border: 1px solid #C58FE6;
			border-radius: 6rem;
			width: 75rem;
			height: 36rem;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 14rem;
			line-height: 145%;
			color: #fff;

			span {
				display: block;
				margin-right: 5rem;
			}
		}

		.noUi-horizontal {
			height: 8rem;
			background: transparent;
			box-shadow: none;
		}

		&-slider {
			margin-top: 30rem;

			.noUi-connect {
				background: #C58FE6;
				border: 1px solid #C58FE6;
				border-radius: 2rem;
			}

			.noUi-handle {
				width: 20rem;
				height: 20rem;
				background: #1D0928;
				border: 1px solid #C58FE6;
				border-radius: 6px;
				box-shadow: none;
				cursor: pointer;

				&::before,
				&::after {
					content: none;
				}
			}

			.noUi-active {
				box-shadow: none;
			}
		}
	}

	&__section+&__section {
		margin-top: 20rem;
	}

	&__submit {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 60rem;
		background: #C58FE6;
		border-radius: 18rem;
		height: 50rem;
		font-family: 'Inter';
		font-weight: 700;
		font-size: 18rem;
		line-height: 24px;
		color: #fff;

		@media (max-width: 991px) {
			background: #8818CD;
			border-radius: 30rem;
		}

		&:hover {
			color: #fff;
		}

		&::before {
			content: '';
			position: absolute;
			top: 10rem;
			left: -10rem;
			width: 100%;
			height: 100%;
			border: 1px solid #C58FE6;
			border-radius: 18rem;
			transition: all 0.3s ease;

			@media (max-width: 991px) {
				content: none;
			}
		}

		&:hover {
			&::before {
				top: 0;
				left: 0;
			}
		}
	}
}