.header {
	position: relative;
	z-index: 1;
	padding-top: 85rem;

	@media (max-width: 991px) {
		padding-top: 15rem;
		padding-bottom: 25rem;
		border-bottom: 1px solid #8818CD;
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 135rem;

		@media (max-width: 991px) {
			padding: 0 20rem;
			justify-content: space-between;
		}
	}

	&__logo {
		position: relative;
		z-index: 20;
		width: 318rem;
		height: 33rem;

		@media (max-width: 991px) {
			width: 138rem;
			height: 14rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__market {
		position: relative;
		font-family: 'Inter';
		font-weight: 700;
		background: #8818CD;
		border-radius: 30rem;
		width: 225rem;
		height: 50rem;
		display: flex;
		align-items: center;
		justify-content: center;

		@media (max-width: 991px) {
			margin-top: 60rem;
			width: 100%;
			height: 140rem;
			font-size: 36rem;
			border-radius: 4rem;
		}

		&::before {
			content: '';
			position: absolute;
			top: 10rem;
			left: -10rem;
			width: 100%;
			height: 100%;
			border: 1px solid #8818CD;
			border-radius: 30rem;
			transition: all 0.3s ease;

			@media (max-width: 991px) {
				content: none;
			}
		}

		&:hover {
			color: #fff;

			&::before {
				top: 0;
				left: 0;
			}
		}
	}

	&__btn {
		display: none;

		@media (max-width: 991px) {
			position: relative;
			z-index: 20;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 40rem;
			height: 40rem;
			background: #8818cd;
			border-radius: 2rem;

			&::after {
				content: '';
				position: absolute;
				top: 5rem;
				left: -5rem;
				width: 100%;
				height: 100%;
				border: 1px solid #8818CD;
				border-radius: 2rem;
			}
		}

		&--search {
			z-index: 1;
		}

		img {
			width: 20rem;
			height: 20rem;
		}
	}

	&__home {
		position: relative;
		padding-left: 30rem;
		margin-right: 50rem;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18rem;
		line-height: 150%;
		color: #DDEAE9;

		@media (max-width: 991px) {
			display: none;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/home.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 24rem;
			height: 24rem;
		}
	}

	&__wallet {
		position: relative;
		font-family: 'Inter';
		font-weight: 700;
		background: #8818CD;
		border-radius: 30rem;
		width: 183rem;
		height: 50rem;
		display: flex;
		align-items: center;
		justify-content: center;

		&:hover {
			color: #fff;
			opacity: 0.8;
		}

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__search {
		&.active {
			.menu__form {
				opacity: 1;
				visibility: visible;
				top: 90rem;
			}

			.menu__search-close {
				display: block;
			}

			.menu__search-search {
				display: none;
			}
		}
	}
}


.menu {
	margin: 0 55rem;

	@media (max-width: 991px) {
		position: relative;
		margin: 0;
		order: -1;
	}

	&__body {
		@media (max-width: 991px) {
			background-color: #390B56;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 130rem 20rem 20rem;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #390B56;
				border-bottom: 1px solid #8818CD;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 81rem;
				z-index: 10;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}

			&--market {
				padding-top: 80rem;
				padding-left: 0;
				padding-right: 0;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__form {
		position: relative;

		@media (max-width: 991px) {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			top: 100rem;
			left: 0;
			width: 100%;
			transition: all 0.3s ease;
			padding: 0 15rem;
			z-index: 1;
		}
	}

	&__input {
		width: 460rem;
		height: 64rem;
		background: #050808;
		border: 1px solid #8818CD;
		border-radius: 30rem;
		padding: 0 100rem 0 24rem;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18rem;
		line-height: 135%;
		color: #D2D4D4;
		outline: none;

		@media (max-width: 991px) {
			width: 100%;
		}
	}

	&__search {
		position: absolute;
		top: 50%;
		right: 20rem;
		transform: translateY(-50%);
		background: #8818CD;
		border-radius: 30rem;
		width: 44rem;
		height: 44rem;
		display: flex;
		align-items: center;
		justify-content: center;

		&-close {
			display: none;
		}

		&::before {
			content: '';
			position: absolute;
			top: 3rem;
			left: -5rem;
			width: 100%;
			height: 100%;
			border: 1px solid #8818CD;
			border-radius: 30rem;
			transition: all 0.3s ease;
		}

		&:hover {
			&::before {
				top: 0;
				left: 0;
			}
		}
	}

	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li+li {
			@media (min-width: 992px) {
				margin-left: 60rem;
			}

			@media (max-width: 991px) {
				margin-top: 30rem;
			}
		}
	}

	&__link {
		font-family: 'Inter';
		font-weight: 500;
		color: #D3E2E2;

		@media (max-width: 991px) {
			display: block;
			padding: 30rem;
			background: #8818CD;
			box-shadow: 0px 1px 5px #CD9FEA;
			border-radius: 45rem;
			font-family: 'BoomBox';
			font-size: 20rem;

			&:hover {
				color: #D3E2E2;
			}
		}
	}

	&__icon {
		@media (max-width: 991px) {
			&-bg {
				width: 40rem;
				height: 40rem;
				background: #8818CD;
				border-radius: 2rem;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: 5;


				&::after {
					content: '';
					position: absolute;
					top: 5rem;
					left: -5rem;
					width: 100%;
					height: 100%;
					border: 1px solid #8818CD;
					border-radius: 2rem;
				}
			}

			z-index: 5;
			display: block;
			position: relative;
			width: 35rem;
			height: 20rem;
			cursor: pointer;
			z-index: 6;

			span,
			&:before,
			&:after {
				left: 0;
				position: absolute;
				height: 10%;
				width: 100%;
				transition: all 0.3s ease 0s;
				background-color: #D2D4D4;
				border-radius: 10rem;
			}

			&:before,
			&:after {
				content: "";
			}

			&:before {
				top: 0;
			}

			&:after {
				bottom: 0;
			}

			span {
				top: 50%;
				transform: scale(1) translate(0, -50%);
			}

			&.active span {
				transform: scale(0) translate(0, -50%);
			}

			&.active:before {
				top: 50%;
				transform: rotate(-45deg) translate(0, -50%);
			}

			&.active:after {
				bottom: 50%;
				transform: rotate(45deg) translate(0, 50%);
			}
		}
	}
}