.roadmap {
	padding-top: 100rem;

	&__slide {
		padding: 0 25rem;
	}

	&__date {
		display: flex;
		position: relative;
		padding-bottom: 80rem;
		font-size: 36rem;
		line-height: 150%;
		color: #4E0D76;

		&:after,
		&::before {
			content: '';
			position: absolute;
			transform: rotate(45deg);
		}

		&::before {
			top: 77rem;
			left: 5rem;
			width: 10rem;
			height: 10rem;
			background: #8818CD;
			z-index: 1;
		}

		&:after {
			top: 72rem;
			left: 0;
			width: 20rem;
			height: 20rem;
			background: #172020;
		}
	}

	&__date+&__date {
		margin-left: 90rem;
	}

	&__month {
		background: linear-gradient(101.03deg, #C58FE6 1.17%, rgba(78, 13, 118, 0.68) 90.25%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		background-clip: text;
		text-fill-color: transparent;
	}

	&__year {
		margin-left: 10rem;
		color: #D2D4D4;
	}

	&__swiper {
		padding: 80rem 0 130rem;

		&::before {
			content: '';
			background-image: url('../img/line.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: 100% 100%;
			position: absolute;
			top: 160rem;
			left: 0;
			width: 100%;
			height: 3rem;
		}
	}

	&__list {
		margin: auto;
		background: #1D0928;
		border: 2px solid #4E0D76;
		box-shadow: 0px 0px 20px #8818CD;
		border-radius: 30rem;
		padding: 30rem;
	}

	&__item {
		cursor: default;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18rem;
		line-height: 150%;
		color: #DDEAE9;
		transition: all 0.3s ease;

		&:hover {
			color: #8818cd;
		}
	}

	&__item+&__item {
		margin-top: 20rem;
	}

	&__nav {
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 1165rem;
		margin: 40rem auto 0;
		padding: 0 15rem;
	}

	.swiper-button-prev,
	.swiper-button-next {
		position: inherit;
		background-size: 27rem;
		background-position: center;
		background-repeat: no-repeat;
		transition: all .3s ease;
		width: 100%;
		max-width: 60rem;
		height: 60rem;
		background-color: rgba(255, 255, 255, .16);
		border-radius: 8rem;
		cursor: pointer;
		z-index: 10;
		margin: 0;

		&:hover {
			background-color: #8818CD;
		}

		&::after {
			content: none;
		}
	}

	.swiper-button-prev {
		background-image: url('../img/svg/arrow-left.svg');
	}

	.swiper-button-next {
		background-image: url('../img/svg/arrow-right.svg');
	}

	.swiper-pagination {
		position: inherit;
		background: #0D1414;
		border-radius: 4rem;
		width: 960rem;
		margin: auto;
		display: flex;
		padding: 5rem;

		@media (max-width: 991px) {
			width: 360rem;
		}
	}

	.swiper-pagination-bullet {
		background: #8818CD;
		border-radius: 4rem;
		width: 100%;
		text-align: left;
	}
}