.footer {
	padding: 150rem 0 50rem;

	@media (max-width: 991px) {
		padding-top: 100rem;
	}

	&__content {
		max-width: 1295rem;
		margin: auto;
		padding: 0 15rem;
	}

	&__logo {
		display: block;
		width: 318rem;
		height: 33rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__top {
		margin-top: 40rem;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__subtitle {
		font-size: 24rem;
		line-height: 150%;
		color: #C58FE6;
		margin-bottom: 25rem;
	}

	&__form {
		position: relative;
	}

	&__input {
		width: 560rem;
		height: 90rem;
		padding: 0 210rem 0 20rem;
		background: #050808;
		border: 1px solid #4E0D76;
		border-radius: 30rem;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18rem;
		line-height: 150%;
		color: #D2D4D4;
		outline: none;

		@media (max-width: 991px) {
			width: 100%;
		}
	}

	&__btn {
		width: 154rem;
		height: 48rem;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #8818CD;
		border-radius: 30rem;
		font-family: 'Inter';
		font-weight: 600;
		font-size: 18rem;
		color: #fff;
		position: absolute;
		top: 50%;
		right: 30rem;
		transform: translateY(-50%);
		transition: all 0.3s ease;

		&:hover {
			opacity: 0.8;
		}
	}

	&__left {
		@media (max-width: 991px) {
			margin-bottom: 40rem;
		}
	}

	&__socials {
		display: flex;
		align-items: center;
		justify-content: center;

		@media (max-width: 991px) {
			justify-content: space-between;
		}
	}

	&__social {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #8818CD;
		border-radius: 30rem;
		width: 115rem;
		height: 80rem;
		z-index: 1;

		@media (max-width: 991px) {
			width: 95rem;
			height: 60rem;
		}

		img {
			width: 32rem;
			height: 30rem;
		}

		&::before {
			content: '';
			position: absolute;
			top: 5rem;
			left: -10rem;
			width: 100%;
			height: 100%;
			border: 1px solid #8818CD;
			border-radius: 30rem;
			transition: all 0.3s ease;
			z-index: -1;

			@media (max-width: 991px) {
				top: 10rem;
				left: 0;
			}
		}

		&:hover {
			&::before {
				top: 0;
				left: 0;
			}
		}
	}

	&__social+&__social {
		@media (min-width: 992px) {
			margin-left: 20rem;
		}
	}

	&__bottom {
		padding-top: 100rem;
		display: flex;
		justify-content: space-between;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__copy {
		font-family: 'Inter';
		font-weight: 700;
		font-size: 18rem;
		line-height: 140%;
		color: #C58FE6;
	}

	&__text {
		font-size: 12rem;
		line-height: 140%;
		color: #C58FE6;

		@media (max-width: 991px) {
			margin-top: 20rem;
		}
	}
}