.intro {
	position: relative;
	padding-top: 300rem;

	&::before {
		content: '';
		background-image: url('../img/stars.png');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: -140rem;
		left: 0;
		width: 100%;
		height: 100%;
	}

	&::after {
		@media (max-width: 991px) {
			content: '';
			background-image: url('../img/nlo.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			bottom: 90rem;
			left: 50%;
			transform: translateX(-50%);
			width: 196rem;
			height: 214rem;
		}
	}

	&__face {
		&-img {
			position: absolute;
			top: -250rem;
			left: 50%;
			transform: translateX(-50%);
			width: 159rem;
			height: 195rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__ufo {
		&-img {
			position: absolute;
			top: 90rem;
			left: 380rem;
			width: 269rem;
			height: 277rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__content {
		max-width: 1220rem;
		margin: auto;
	}

	&__title {
		font-size: 64rem;
		line-height: 150%;
		text-align: center;
		letter-spacing: 0.05em;
		background: linear-gradient(101.71deg, #CD9FEA 1.23%, #8818CD 94.23%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		background-clip: text;
		text-fill-color: transparent;

		@media (max-width: 991px) {
			font-size: 42rem;
		}
	}

	&__desc {
		margin: 20rem auto 0;
		max-width: 470rem;
		font-family: 'Inter';
		font-weight: 500;
		font-size: 18rem;
		line-height: 150%;
		text-align: center;
		letter-spacing: 0.1em;
		color: #7E6C8A;

		@media (max-width: 991px) {
			padding-bottom: 250rem;
		}
	}
}