.pairs {
	padding-top: 100rem;

	&__title {
		color: #DDEAE9;
		-webkit-text-fill-color: inherit;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(5, 1fr);
		grid-gap: 60rem;
		max-width: 1370rem;
		margin: 60rem auto 0;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 20rem;
			justify-content: center;
		}
	}

	&__item {
		cursor: pointer;
		background: rgba(60, 10, 90, 0.6);
		border: 11px solid #C58FE6;
		box-shadow: 0px 4px 15px #C58FE6;
		border-radius: 124px;
		width: 226rem;
		height: 226rem;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		transition: all 0.3s ease;

		&:hover {
			box-shadow: 0px 1px 5px #C58FE6;
		}

		@media (max-width: 991px) {
			margin: auto;
		}
	}

	&__icon {
		width: 92rem;
		height: 92rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__name {
		margin-top: 7rem;
		font-family: 'Inter';
		font-weight: 400;
		font-size: 20rem;
		line-height: 150%;
		align-items: center;
		color: #fff;
	}
}